/* Global Styles */
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.nav-button {
  margin-left: 1em;
  margin-right: 1em;
}

#fixedButton {
  position: fixed;
  bottom: 1em;
  right: 1em;
}

.question {
  padding-left: 1em;
  padding-right: 1em;
}

.votes {
  color: red;
}

.questions-list {
  margin-bottom: 7.5em;
}

.clickable {
  cursor: pointer;
}
